___CSS_LOADER_EXPORT___.push([module.id, ".modal-overlay{display:flex;align-items:center;justify-content:center;position:fixed;z-index:5000;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.45)}.modal-window{z-index:10000;margin:1.5em auto 0;border:2px solid #aaa;background:#fff;max-height:90%;position:relative;overflow-y:auto}.modal-content,.modal-window{padding:10px 20px}.modal-enter-active,.modal-leave-active{transition:opacity .4s}.modal-leave-active{transition:opacity .6s ease .4s}.modal-enter,.modal-leave-to{opacity:0}.close-button{position:absolute;top:0;right:0;border-radius:3px;background-color:red;color:#fff;padding:2px 10px;font-size:20px}.close-button:hover{color:#fff}.confirm-btn{outline:none}", "",{"version":3,"sources":["webpack://./resources/js/components/parts/modal_hour.vue"],"names":[],"mappings":"AAiGA,eACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,cAAA,CACA,YAAA,CACA,KAAA,CACA,MAAA,CACA,UAAA,CACA,WAAA,CACA,0BACA,CAEA,cACA,aAAA,CACA,mBAAA,CAEA,qBAAA,CACA,eAAA,CACA,cAAA,CACA,iBAAA,CACA,eACA,CAEA,6BARA,iBAUA,CAGA,wCACA,sBACA,CAGA,oBACA,+BACA,CAEA,6BACA,SACA,CACA,cACA,iBAAA,CACA,KAAA,CACA,OAAA,CACA,iBAAA,CACA,oBAAA,CACA,UAAA,CACA,gBAAA,CACA,cACA,CAEA,oBACA,UACA,CAEA,aACA,YACA","sourcesContent":["<template>\n  <transition name=\"modal\" appear :scrollable=\"true\">\n    <div class=\"modal modal-overlay\" @click.self=\"$emit('close')\">\n      <div class=\"modal-window\">\n        <div class=\"row mx-auto mb-2\">\n          <input type=\"number\" v-model=\"task.point\">\n          <span>pt</span>\n        </div>\n        <div v-for=\"(item, index) in items\" class=\"row mb-2 mx-auto\">\n          <input type=\"text\" v-model=\"item.title\">\n          <span class=\"mr-2\">：</span>\n          <datetime type=\"datetime\" zone=\"Asia/Tokyo\" v-model=\"item.start\" /> \n          ~\n          <datetime type=\"datetime\" zone=\"Asia/Tokyo\" v-model=\"item.end\" />\n          <button class=\"btn block p-0\">\n            <span class=\"far fa-trash-alt ml-1\" @click=\"deleteHour(index)\"/>\n          </button>\n        </div>\n\n        <!-- 追加ボタン -->\n        <button \n          class=\"btn add-button mb-3\"\n          @click=\"addHour()\"\n        >\n          <span style=\"font-size:30px;\" class=\"far fa-plus-square\"/>\n        </button>\n\n        <!-- 閉じる -->\n        <button class=\"close-button btn\" @click=\"$emit('close')\">\n          <span>×</span>\n        </button>\n\n        <!-- 決定 -->\n        <div class=\"container\">\n          <div class=\"row justify-content-center\">\n            <button class=\"bottom-icon confirm-btn col-md-1 col-2\" @click=\"confirm()\">\n              <span>決定</span>\n            </button>\n          </div>\n        </div>\n      </div>\n    </div>\n  </transition>\n</template>\n\n<script>\nimport moment from \"moment\";\n\nexport default {\n  data(){\n    return {\n      items: [],\n      delete_items: [],\n    };\n  },\n  props:{\n    task: Object,\n    selected_task: Array,\n  },\n  created(){\n    this.items = JSON.parse(JSON.stringify(this.selected_task));\n  },\n  methods:{\n    now(){\n      return moment().format();\n    },\n    addHour(){\n      const task_id = (!this.task.hasOwnProperty('is_new'))? this.task.task_id: null;\n      const new_hour = {\n        'end' :null,\n        'title': this.task.name,\n        'remark': null,\n        'start': this.now(),\n        'task_id': task_id,\n        'is_new': true,\n      };\n\n      this.items.push(new_hour);\n    },\n    deleteHour(index){\n      const item = this.items[index];\n\n      if(!item.hasOwnProperty('is_new')){\n        this.delete_items.push(item);\n      }\n\n      this.$delete(this.items, index);\n    },\n    confirm(){\n      this.$emit('confirm', {items:this.items, delete_hours:this.delete_items});\n      this.$emit('close');\n    },\n  },\n}\n</script>\n\n<style>\n.modal-overlay {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: fixed;\n  z-index: 5000;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.45);\n}\n\n.modal-window {\n  z-index: 10000;\n  margin: 1.5em auto 0;\n  padding: 10px 20px;\n  border: 2px solid #aaa;\n  background: #fff;\n  max-height: 90%;\n  position: relative;\n  overflow-y: auto;\n}\n\n.modal-content {\n  padding: 10px 20px;\n}\n\n/* オーバーレイのトランジション */\n.modal-enter-active, .modal-leave-active {\n  transition: opacity 0.4s;\n}\n\n/* ディレイを付けるとモーダルウィンドウが消えた後にオーバーレイが消える */\n.modal-leave-active {\n  transition: opacity 0.6s ease 0.4s;\n}\n\n.modal-enter, .modal-leave-to {\n  opacity: 0;\n}\n.close-button{\n  position: absolute;\n  top: 0px;\n  right: 0px;\n  border-radius: 3px;\n  background-color: red;\n  color: white;\n  padding: 2px 10px;\n  font-size: 20px;\n}\n\n.close-button:hover{\n  color: white;\n}\n\n.confirm-btn{\n  outline:none;\n}\n\n</style>"],"sourceRoot":""}]);
